// @flow

import React from 'react'
import { DisplayTable } from '../DisplayTable'

import './style.scss'

type TableData = {
  title: string,
  headers: string[],
  data: Object[]
}

type Props = {
  tableData: TableData
}

export const Section = (props: Props) => {
  const { tableData } = props

  return (
    <div className="Section">
      <h2>{tableData.title}</h2>
      <DisplayTable headers={tableData.headers} data={tableData.data} />
    </div>
  )
}
