// @flow

import React from 'react'
import { Table } from 'reactstrap'

type Props = {
  headers: string[],
  data: Object[]
}

export const DisplayTable = (props: Props) => (
  <Table size="sm" responsive striped bordered>
    <thead>
      <tr>
        {props.headers.map((item, idx) => (
          <th key={idx}>{item}</th>
        ))}
      </tr>
    </thead>
    <tbody>
      {props.data.map((item, idx) => (
        <tr key={idx}>
          {Object.values(item).map(val => (
            <td>{val}</td>
          ))}
        </tr>
      ))}
    </tbody>
  </Table>
)
